const initialState={
    input:'',
    coronacases:'',
    status:''
}
export const reducer=(state=initialState,action)=>{
switch(action.type){
    case "UPDATE_INPUT":return{...state,input:action.payload}
    case "FETCH_DATA":return{...state,coronacases:action.payload}
    case "SUBMIT":return{...state,status:action.payload}
    default:return state;

}
}
