import React, { Component } from 'react'
import { connect } from 'react-redux'
import {handleChange,fetchData} from './action'
import './CovidTracker.css'
import {store} from './App'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
class CovidTracker extends Component {
    onChangehandler=e=>{
    this.props.handleChange(e);
    console.log(store.getState())
    }  
    handleSubmit=e=>{
        this.props.fetchData(this.props.userInput);
        e.preventDefault();
    }
  
    render() {
           let key;
           let arr=[];
           let x=this.props.result[259];
           for (key in x)
           {
               console.log(key,x[key]);
               arr.push(x[key]);
           }
        //    console.log('Confirmed',arr[7]);
        //    console.log('Death',arr[8]);
        //    console.log('Recovered',arr[9]);
        //    console.log('Active',arr[10]);
        return (
            <div className="container bg-light mt-5 p-5 border border-additive"> 
            <h1>Covid 19 Tracker App</h1>
            <form  onSubmit={this.handleSubmit}>
            <div className="form-group">
            <label>Enter Your country Name:</label>
            <input className="form-control w-50" onChange={this.onChangehandler} type="text" value={this.props.userInput}/>
            </div>
            <button type="submit" className="btn btn-primary">Submit</button>
            </form>
            <br/>
            <table className="table table-hover">
            <thead>
                <tr>
                <th scope="col">Country Name</th>
                <th scope="col">Confirmed</th>
                <th scope="col">Death</th>
                <th scope="col">Recovered</th>
                <th scope="col">Active</th>
               
                </tr>
            </thead>
            <tbody>
                <tr>
                <th scope="row">{arr[0]}</th>
                <td>{arr[7]}</td>
                <td>{arr[8]}</td>
                <td>{arr[9]}</td>
                <td>{arr[10]}</td>
                </tr>
            </tbody>
   </table>
            </div>
        );
    }
}
const mapDispatchToProps={
    handleChange,
    fetchData
}
function mapStateToProps(state)
{
    
    return{
        userInput:state.input,
        result:state.coronacases
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(CovidTracker);